@charset "UTF-8";
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

:root {
  --primary-color: #a35df6;
  --primary-light: #252d47;
  --primary-dark: #1a1f38;
  --secondary-color: #252d47;
  --grey-color: #eee;
  --white-color: #fefefe;
  --base-height: 50px;
  --error-color: #b3255d;
  --verygood: #49c0b7;
  --good: #4abc6a;
  --average: #c1ba7f;
  --poor: #c65b82; }

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

::-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

:-moz-placeholder {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 400; }

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #383346 inset;
  -webkit-text-fill-color: white !important; }

body {
  position: relative;
  font-family: 'Assistant', sans-serif;
  background: var(--primary-dark);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between; }

p {
  font-size: 18px;
  line-height: 1.8; }

table {
  max-width: 840px;
  margin: 0 auto; }
  table h4 {
    margin: 0;
    font-weight: 300;
    font-size: 12px;
    text-transform: uppercase;
    letter-spacing: 2px;
    min-height: 40px;
    color: rgba(255, 255, 255, 0.6); }

table tr {
  display: flex;
  flex-wrap: wrap; }

table td {
  flex: 1 0 120px;
  padding: 20px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }

table td span:first-of-type {
  font-size: 25px; }

table td span:last-of-type {
  font-size: 32px;
  font-weight: 300; }

.menu {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 30px auto;
  padding: 0; }

.menu a {
  display: block;
  height: 50px;
  padding: 8px 12px;
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 700;
  font-size: 28px; }
  .menu a span {
    display: block;
    height: 0px;
    overflow: hidden;
    transition: 1s 2s ease-in-out; }

.menu.is-active a span {
  height: 50px; }

.page {
  width: 100%;
  max-width: 900px;
  flex: 0 0 80%; }
  .page__title {
    font-size: 100px; }
    .page__title a {
      position: relative;
      text-decoration: none;
      color: var(--primary-light);
      text-shadow: 0 0 40px rgba(0, 0, 0, 0.35); }
    .page__title a::after {
      content: 'beagle';
      color: var(--white-color);
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      font-size: 45%;
      font-weight: 300;
      line-height: 140px; }
  .page__container {
    position: relative;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: var(--primary-light);
    padding: 30px;
    border-radius: 15px;
    box-shadow: 3px 3px 40px 0 rgba(0, 0, 0, 0.3); }

.page__content {
  text-align: center;
  flex: 0 0 50%; }

/*/ Report sent /*/
.has-report .page {
  width: 90vw;
  max-width: 90vw;
  height: 80vh;
  margin-top: 10vh; }

.has-report .report {
  width: 90vw;
  max-width: 90vw;
  margin-top: 10vh; }

.has-report .report {
  left: 5vw;
  bottom: 10vh; }

form {
  position: relative;
  display: flex;
  justify-content: space-between; }

.button {
  flex: var(--base-height);
  max-width: var(--base-height);
  background: rgba(255, 255, 255, 0.07);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border-radius: 0px 10px 10px 0px; }
  .button:focus {
    outline: none;
    box-shadow: 0 0 12px -2px white; }
  .button svg {
    width: 50%;
    height: 50%; }

[type=color], [type=date], [type=datetime-local], [type=datetime], [type=email], [type=month], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=time], [type=url], [type=week], textarea, select {
  height: var(--base-height);
  flex: 70%;
  border: none;
  background: rgba(255, 255, 255, 0.07);
  color: var(--white-color);
  padding-left: 10px;
  border-radius: 10px 0px 0px 10px; }
  [type=color]:focus, [type=date]:focus, [type=datetime-local]:focus, [type=datetime]:focus, [type=email]:focus, [type=month]:focus, [type=number]:focus, [type=password]:focus, [type=search]:focus, [type=tel]:focus, [type=text]:focus, [type=time]:focus, [type=url]:focus, [type=week]:focus, textarea:focus, select:focus {
    outline: none; }
  [type=color]:focus ~ .focusline, [type=date]:focus ~ .focusline, [type=datetime-local]:focus ~ .focusline, [type=datetime]:focus ~ .focusline, [type=email]:focus ~ .focusline, [type=month]:focus ~ .focusline, [type=number]:focus ~ .focusline, [type=password]:focus ~ .focusline, [type=search]:focus ~ .focusline, [type=tel]:focus ~ .focusline, [type=text]:focus ~ .focusline, [type=time]:focus ~ .focusline, [type=url]:focus ~ .focusline, [type=week]:focus ~ .focusline, textarea:focus ~ .focusline, select:focus ~ .focusline {
    width: 100%;
    transition: width 0.1s ease; }

.error {
  position: absolute;
  top: 115%;
  left: 10px;
  font-size: 14px;
  color: var(--error-color); }

.status {
  position: relative;
  font-weight: 700;
  font-size: 14px; }
  .status::before {
    content: 'ðŸ‘'; }
  .status--ready::before {
    content: 'ðŸ’ª'; }

.report {
  display: block;
  width: 100vw;
  background: var(--primary-light);
  border-radius: 15px;
  box-shadow: 3px 3px 40px 0 rgba(0, 0, 0, 0.3); }

.report__header {
  padding: 3px 40px;
  background: #ffffff17;
  color: #949cb5;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px; }
  .report__header h3 {
    font-size: 22px;
    font-weight: 300; }
  .report__header h3 span {
    color: #ffffffab; }

.report__container {
  display: grid;
  grid-template-rows: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin: 20px; }

.report__footer {
  padding: 30px 0; }

.report__button {
  color: var(--white-color);
  text-decoration: none;
  font-weight: bold;
  font-size: 18px;
  margin: 0 15px; }

.panel {
  background: #0000001a; }

.panel__header {
  margin: 0 0 10px;
  padding: 20px;
  text-align: center;
  background: #0000001a; }
  .panel__header h4 {
    font-weight: 300;
    color: #ffffffb5;
    margin: 0; }

.footer {
  min-height: 10vh;
  padding: 30px 0;
  opacity: 0.3; }

.loading {
  position: absolute;
  z-index: 66;
  top: 30%;
  left: 0;
  width: 100%;
  text-align: center;
  opacity: 0;
  height: 0;
  transition: opacity 0.8s 0s;
  pointer-events: none; }
  .loading h5 {
    color: var(--white-color);
    font-size: 26px;
    font-weight: 300; }
  .loading h5 span {
    display: block;
    margin: 10px 0;
    opacity: 0.4;
    font-size: 14px; }

.loading.is-active {
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 1;
  transition: opacity 0.8s 1.5s; }

.loading__animation {
  margin: 40px auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  filter: url(#goo);
  width: 200px;
  height: 100px; }

.loading__animation div {
  background: #fffff0;
  width: 20px;
  height: 20px;
  border-radius: 50%; }

.loading__animation > div {
  transform: translateX(0);
  animation: sideways 2.5s infinite ease; }

.loading__animation > div:nth-child(1) {
  animation-delay: .25s; }

.loading__animation > div:nth-child(2) {
  animation-delay: .5s; }

.loading__animation > div:nth-child(3) {
  animation-delay: .75s; }

.loading__animation > div:nth-child(4) {
  animation-delay: 1s; }

.loading__animation > div:nth-child(5) {
  animation-delay: 1.25s; }

@keyframes circle {
  50% {
    width: 25px;
    height: 25px; }
  100% {
    transform: rotate(360deg); } }

@keyframes sideways {
  33% {
    transform: translateX(50px); }
  66% {
    transform: translateX(-50px); } }

.dialog {
  max-width: 600px;
  border: none;
  box-shadow: 0px 0px 20px 0px #00000078;
  text-align: center; }
  .dialog h4 {
    color: var(--error-color);
    font-size: 28px;
    margin-top: 0; }
  .dialog menu {
    padding-left: 0; }

.dialog__button {
  background: #261f33;
  color: white;
  border: none;
  padding: 12px 25px;
  font-size: 13px;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 700; }

.dialog__content strong {
  margin-right: 10px;
  text-transform: uppercase;
  font-size: 80%;
  opacity: 0.8; }

.audit {
  height: 350px;
  margin: 20px 40px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column; }

[class^="audit__item"] {
  margin: 0 0 15px; }
  [class^="audit__item"]:hover .summary p {
    color: var(--white-color); }

.audit__item--pass .summary {
  color: var(--verygood); }

.audit__item--fail .summary {
  color: var(--poor); }

.audit__score {
  float: right;
  font-size: 20px;
  font-weight: 700;
  margin: 0 0 20px 20px; }

.summary {
  display: flex;
  align-items: center; }
  .summary p {
    transition: all 0.2s ease;
    font-size: 16px;
    margin: 0;
    color: #ffffff63; }
